import React, { useEffect } from "react"
import Section from "../../components/Section"
import { Row, Col } from "antd"
import { Link, withRouter } from "react-router-dom"
import { Heading, Button, Card, GifPlayer } from "components"
import { Trans } from "@lingui/macro"
import { country } from "App"

function PasswordChanged({ history, location = {} }) {
	useEffect(() => {
		const state = location.state || {}
		if (!state.success) history.push(`/${country}`)
	}, [history, location.state])

	return (
		<Section style={{ marginTop: "4em" }}>
			<Row type="flex" justify="center" align="middle">
				<Col span={8}>
					<Card title="" autoHeight>
						<Section centered>
							<GifPlayer src="https://assets9.lottiefiles.com/temp/lf20_4Jjg2l.json" width={250} height={250} class="mb2em" />
							<Heading
								content={<Trans>It's changed!</Trans>}
								subheader={<Trans>Great! Now you can try to login using your brand new password. Have fun! :)</Trans>}
							/>
						</Section>
						<Section centered paddingHorizontal="normal">
							<Link to={`/${country}/login`}>
								<Button type="ghost" icon="home">
									<Trans>Go to login</Trans>
								</Button>
							</Link>
						</Section>
					</Card>
				</Col>
			</Row>
		</Section>
	)
}

export default withRouter(PasswordChanged)
